<template>
    <div id="hj-index-1">
        <h2 id="hj-index-1-title">{{module.text[0].children[0].value}}<span>{{module.text[0].children[1].value}}</span></h2>
        <el-carousel arrow="never" indicator-position="outside">
            <el-carousel-item v-for="(item,index) in articleList" :key="index">
                <div v-for="children in item" class="banner-item" v-bind:key="children.title">
                    <div class="img">
                        <img :src="children.pic" :alt="children.title">
                    </div>

                    <div class="text">
                        <h3>{{children.title}}</h3>
                        <p>{{children.description}}</p>
                    </div>

                </div>
            </el-carousel-item>
        </el-carousel>
    </div>
</template>

<script>
    import {elementAnimate} from "../../../src/assets/js/elementAnimate";
    export default {
        name: "hj-index-1",
        data() {
            return {
                module: {
                    name: '',
                    classId: 'hj-index-1',
                    type: 'list',
                    article: [
                        {
                            pic: 'http://www.hejigy.com/Img/index/service1.jpg',
                            title: '',
                            description: ''
                        },
                        {
                            pic: 'http://www.hejigy.com/Img/index/service2.jpg',
                            title: '',
                            description: ''
                        },
                        {
                            pic: 'http://www.hejigy.com/Img/index/service3.jpg',
                            title: '',
                            description: ''
                        },
                        {
                            pic: 'http://www.hejigy.com/Img/index/service4.jpg',
                            title: '',
                            description: '.'
                        },
                    ],
                    text:[
                        {
                            label:'',
                            type:'default',
                            children:[
                                {
                                    label: '',
                                    value:'/'
                                },
                                {
                                    label: '',
                                    value:'service scope'
                                },
                            ]
                        }
                    ],
                    style:[
                        {
                            label:'',
                            name:'--leftColor',
                            value:'rgba(84,84,84,1)',
                            property:'color'
                        },
                        {
                            label:'',
                            name:'--leftFontSize',
                            value:'20px',
                        },
                        {
                            label:'',
                            name:'--rightColor',
                            value:'rgb(149,208,255)',
                            property:'color'
                        },
                        {
                            label:'',
                            name:'--rightFontSize',
                            value:'24px',
                        },
                    ],
                    info: [
                        '',
                        '',
                        ''
                    ]
                }
            }
        },
        computed:{
            articleList:function () {
                let pageLimit=3
                let loopTime=Math.floor(this.module.article.length/pageLimit)
                let arr=[]
                for (let i = 0; i <loopTime ; i++) {
                    arr[i]=[]
                    for (let j = 0; j <pageLimit ; j++) {
                        arr[i].push(this.module.article[i*pageLimit+j])
                    }
                }
                if(this.module.article.length%pageLimit!==0){
                    arr.push(this.module.article.slice(this.module.article.length-(this.module.article.length%pageLimit)))
                }

                return arr
            }
        },
        mounted() {
            new elementAnimate([document.getElementById('hj-index-1-title')],'titleTranslate 1s forwards')
        }
    }
</script>

<style scoped>
    #hj-index-1 {
        --leftColor:'';
        --leftFontSize:'';
        --rightColor:'';
        --rightFontSize:'';


        width: 1200px;
        height: 540px;
        margin: 0 auto;

    }
    #hj-index-1 h2{
        font-size: var(--leftFontSize);
        color: var(--leftColor);
        font-weight: normal;
        margin-bottom: 30px;
        margin-top: 30px;
        text-align: center;
    }
    #hj-index-1 h2 span{
        font-size: var(--rightFontSize);
        margin-left: 5px;
        color: var(--rightColor);
    }
    .banner-item{
        width: 370px;
        display: inline-block;
        border-bottom: 1px solid #B5B5B5;

    }
    .banner-item img{
        width: 370px;
        height: 230px;
        transition: 1s;
    }
    .banner-item .img{
        overflow: hidden;
    }
    .banner-item img:hover{
        transform: scale(1.3);
    }
    .banner-item h3{
        font-size: 18px;
        font-weight: normal;
        cursor: pointer;
        display: inline-block;
    }
    .banner-item h3:hover{
        color: #15909c;
    }
    .banner-item p{
        font-size: 12px;
        color: #5E5E5E;
        line-height: 24px;
        margin-top: 0;
    }
    .banner-item>.text{
        padding: 0 20px;
    }
</style>
<style>
    #hj-index-1 .el-carousel__item{
        display: flex;
        justify-content: space-between;
    }
    #hj-index-1 .el-carousel__container{
        max-height: 425px;
        overflow: hidden;
    }
    #hj-index-1 .el-carousel__button {
        width: 14px;
        height: 14px;
        opacity: 1;
        border-radius: 999px;
        border-color:rgba(104,171,250,1) ;
        border-width: 1px;
        border-style: solid;
        background-color: #fff;
        transform: translateY(18px);
    }

    #hj-index-1 .el-carousel__indicator.is-active button {
        background-color: rgba(104,171,250,1);
    }

</style>
