<template>
    <div id="hj-index-3">
        <h2 id="hj-index-3-title">{{module.text[0].children[0].value}}<span>{{module.text[0].children[1].value}}</span>
        </h2>
        <div id="hj-index-3-content">
            <div>
                <div v-for="item in module.article" class="hj-index-3-item" v-bind:key="item.title">
                    <div>
                        <img :src="item.pic" alt="">
                        <span>{{item.releaseTime}}</span>
                        <h3>
                            {{item.title}}
                        </h3>
                        <p>
                            {{item.description}}
                        </p>
                    </div>



                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {elementAnimate} from "../../../src/assets/js/elementAnimate";

    export default {
        name: "hj-index-3",
        data() {
            return {
                module: {
                    name: '',
                    classId: 'hj-index-3',
                    type: 'list',
                    limit: 6,
                    text: [
                        {
                            label: '',
                            type: 'default',
                            children: [
                                {
                                    label: '',
                                    value: '/'
                                },
                                {
                                    label: '',
                                    value: 'News'
                                },
                            ]
                        }
                    ],
                    style: [
                        {
                            label: '',
                            name: '--leftColor',
                            value: 'rgba(84,84,84,1)',
                            property: 'color'
                        },
                        {
                            label: '',
                            name: '--leftFontSize',
                            value: '20px',
                        },
                        {
                            label: '',
                            name: '--rightColor',
                            value: 'rgba(149,208,255,1)',
                            property: 'color'
                        },
                        {
                            label: '',
                            name: '--rightFontSize',
                            value: '24px',
                        },
                    ],
                    article: [
                        {
                            title: 'LED',
                            pic: 'http://www.hejigy.com/Img/index/time.png',
                            releaseTime: '202119 08:45:38',
                            description: ' 50mNA0.03'
                        },
                        {
                            title: 'LED2',
                            pic: 'http://www.hejigy.com/Img/index/time.png',
                            releaseTime: '202119 08:45:37',
                            description: ' 50mNA0.03'
                        },
                        {
                            title: 'LED3',
                            pic: 'http://www.hejigy.com/Img/index/time.png',
                            releaseTime: '202119 08:45:3',
                            description: ' 50mNA0.03'
                        },
                        {
                            title: 'LED4',
                            pic: 'http://www.hejigy.com/Img/index/time.png',
                            releaseTime: '202119 08:45:38',
                            description: ' 50mNA0.03'
                        },
                        {
                            title: 'LED5',
                            pic: 'http://www.hejigy.com/Img/index/time.png',
                            releaseTime: '202119 08:45:38',
                            description: ' 50mNA0.03'
                        },
                        {
                            title: 'LED6',
                            pic: 'http://www.hejigy.com/Img/index/time.png',
                            releaseTime: '202119 08:45:38',
                            description: ' 50mNA0.03'
                        },
                    ]
                }
            }
        },
        mounted() {
            new elementAnimate([document.getElementById('hj-index-3-title')], 'titleTranslateNoVolume 1s forwards')
            new elementAnimate([document.getElementById('hj-index-3-content')], 'RightFormFlyIn 2s forwards')
        }
    }
</script>

<style scoped>
    #hj-index-3 {
        --leftColor: '';
        --leftFontSize: '';
        --rightColor: '';
        --rightFontSize: '';


        margin: 0 auto;

    }

    #hj-index-3 h2 {
        font-size: var(--leftFontSize);
        color: var(--leftColor);
        font-weight: normal;
        margin-bottom: 30px;
        margin-top: 30px;
        text-align: center;
    }

    #hj-index-3 h2 span {
        font-size: var(--rightFontSize);
        margin-left: 5px;
        color: var(--rightColor);
    }

    #hj-index-3 > div {
        height: 330px;
        background-color: rgb(224, 234, 246);
        background-image: url('http://www.hejigy.com/Img/index/news_bg_fixed.jpg');
        background-size: 100% 100%;
        padding: 45px 0;
    }

    #hj-index-3 > div > div {
        width: 1200px;
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        align-items: center;
        height: 100%;
    }

    #hj-index-3 .hj-index-3-item {
        width: 330px;
        height: 140px;
        background-color: #fff;
        text-align: center;
        cursor: pointer;
        display: inline-block;
        overflow: hidden;
        padding: 0 25px;
        transition: .5s;
        /*vertical-align: middle;*/
        /*margin-top: 30px;*/
    }
    .hj-index-3-item> div{
        transition: .5s;
    }

    .hj-index-3-item> div> img {
        margin-top: 20px;
        margin-bottom: 12px;
    }

    .hj-index-3-item>div > span ,.hj-index-3-item>div>p{
        display: block;
        color: #666;
        font-size: 12px;
        line-height: 24px;
    }
    .hj-index-3-item>div>p{
        text-align: left;
    }
    .hj-index-3-item>div > h3 {
        font-size: 18px;
        font-weight: normal;
    }
    #hj-index-3 .hj-index-3-item:hover {
        height: 160px;
        margin: -999px 0;
    }
    #hj-index-3 .hj-index-3-item:hover div {
        transform: translateY(-82px);
    }

    #hj-index-3 .hj-index-3-item:hover h3 {
        color: #2eafbb;
    }
</style>
